import React from "react";
import axios from "axios";
import "../assets/main.css"

async function handleSubmit(e) {
    e.preventDefault();
    // Modal
    // Get the modal
    var modal = document.getElementById("myModal");

    // Get the <span> element that closes the modal
    var span = document.getElementsByClassName("close")[0];

    // When the user clicks on the button, open the modal
    modal.style.display = "block";

    // When the user clicks on <span> (x), close the modal
    span.onclick = function () {
        modal.style.display = "none";
    }

    // When the user clicks anywhere outside of the modal, close it
    window.onclick = function (event) {
        if (event.target === modal) {
            modal.style.display = "none";
        }
    }
    const name = document.getElementById('name').value;
    const email = document.getElementById('email').value;
    const message = document.getElementById('message').value;
    // console.log(`message: ${message}`)
    await axios({
        method: "POST",
        url: "/send",
        data: {
            name: name,
            email: email,
            message: message
        }
    }).then((response) => {
        if (response.data.msg === 'success') {
            // alert("Message Sent.");
            document.getElementById("modalText").innerHTML = "<p>Sent <i class='green fa fa-check'></i></p>"
            resetForm()
        } else if (response.data.msg === 'fail') {
            // alert("Message failed to send.")
            document.getElementById("modalText").innerHTML = "<p>Message failed to send <i class='red fa fa-times'></i></p>"
        }
    })

}

function resetForm() {
    document.getElementById('contact-form').reset();
}


const Contact = () => (
    <div className="contact">
        <p className="contact-title">Contact Us</p>
        <form id="contact-form" onSubmit={(event) => handleSubmit(event)} method="POST">
            <div className="form-group">
                <label htmlFor="name">Name</label>
                <input type="text" className="form-control" id="name" required={true}/>
            </div>
            <div className="form-group">
                <label htmlFor="exampleInputEmail1">Email address</label>
                <input type="email" className="form-control" id="email" aria-describedby="emailHelp" required={true}/>
            </div>
            <div className="form-group">
                <label htmlFor="message">Message</label>
                <textarea className="form-control" rows="5" id="message" required={true}></textarea>
            </div>
            <button type="submit" id="submit" className="btn btn-primary">Submit</button>
            {/* modal */}
            <div id="myModal" className="modal">
                <div className="modal-content">
                    <span className="close">&times;</span>
                    <p id="modalText">Sending.. <i className='icon fa fa-hourglass-half'></i></p>
                </div>

            </div>
        </form>
    </div>
);


export default Contact;
